import { PokemonsResponse, AbilityResponse } from "@/app/products/toy/interfaces/pokemons-response";
import { SimplePokemon } from "@/app/products/toy/interfaces/simple-pokemons";
import { PokemonItem } from "@/app/components/PokemonItem";


const getPokemons = async (limit = 50, offset = 0): Promise<SimplePokemon[]> => {
  const data: PokemonsResponse = await fetch(`https://pokeapi.co/api/v2/pokemon?limit=${limit}&offset=${offset}`)
    .then(response => response.json());

  const pokemons = data.results.map(pokemon => ({
    id: pokemon.url.split('/').at(-2)!,
    name: pokemon.name,
  }));

  return pokemons;
};


const getAbilityFlavorTexts = async (abilityName: string): Promise<string[]> => {
  const response: AbilityResponse = await fetch(`https://pokeapi.co/api/v2/ability/${abilityName}`)
    .then(res => res.json());

  const textosEnEspañol = response.flavor_text_entries.filter(entry => entry.language.name === "es");

  return textosEnEspañol.map(entry => entry.flavor_text);
};

export default async function Page() {
  const data = await getPokemons();
  const textos = await getAbilityFlavorTexts("battle-armor");

  console.log("Pokemons:", data);
  console.log("Ability (battle-armor):", textos);

  return (
    <div>
      <h1 className="text-xl font-bold mb-4">Habilidad: Battle Armor</h1>
      <ul className="mb-6 list-disc list-inside">
        {textos.map((text, idx) => (
          <li key={idx}>{text}</li>
        ))}
      </ul>

      <ul>
        {data.map(item => <PokemonItem key={item.id} {...item} />)}
      </ul>
    </div>
  );
}
