import { useEffect, useState } from "react";
import { SimplePokemon } from "../products/toy/interfaces/simple-pokemons";

export const PokemonItem = ({ id, name }: SimplePokemon) => {
    const [abilityText, setAbilityText] = useState("Cargando habilidad...");
    const [gameIndex, setGameIndex] = useState<number | null>(null);
    const [versionName, setVersionName] = useState<string>("???");

    useEffect(() => {
        const fetchData = async () => {
            try {
                const pokemonData = await fetch(`https://pokeapi.co/api/v2/pokemon/${id}`).then(res => res.json());

                const abilityName = pokemonData.abilities[0]?.ability?.name;
                if (abilityName) {
                    const abilityData = await fetch(`https://pokeapi.co/api/v2/ability/${abilityName}`).then(res => res.json());
                    const flavorEntry = abilityData.flavor_text_entries.find((entry: any) => entry.language.name === "es");
                    setAbilityText(flavorEntry?.flavor_text || "Habilidad sin descripción.");
                }

                if (pokemonData.game_indices && pokemonData.game_indices.length > 0) {
                    const firstIndex = pokemonData.game_indices[0];
                    setGameIndex(firstIndex.game_index);
                    setVersionName(firstIndex.version.name);
                }

            } catch (err) {
                console.error("Error al obtener los datos:", err);
                setAbilityText("Error al cargar la habilidad.");
            }
        };

        fetchData();
    }, [id]);

    return (
        <div>
            <section className="">
                <div className="container mx-auto py-10 bg-neutral-800/50">
                    <div className="flex px-4 flex-wrap">
                        <div className="w-full overflow-hidden flex justify-center items-center ring-8 ring-[#252436BF] bg-[#B8693E] shadow-lg md:w-1/2 lg:w-1/4">
                            <div className="bg-[url('https://i.imgur.com/DNVGE5B.png')] bg-repeat">
                                <div className="mx-5 mt-2 flex justify-between items-center shadow-md border-2 px-2 pt-1 rounded-[2px] border-opacity-50 border-t-teal-50 border-l-teal-50 border-b-[#000000] border-r-[#000000]">
                                    <span className="font-Merriweather font-bold text-lg">{name}</span>
                                    <span>
                                        <img
                                            src="https://static.wikia.nocookie.net/yugioh/images/a/a1/EARTH.svg"
                                            alt={name}
                                            className="mx-7 my-1 flex justify-end items-center"
                                            height="30"
                                            width="30"
                                        />
                                    </span>
                                </div>
                                <div className="mx-7 my-1 flex justify-end items-center">
                                    <span>
                                        <img
                                            src="https://upload.wikimedia.org/wikipedia/commons/7/7b/Starball_Red.svg"
                                            alt={name}
                                            className="mx-7 my-1 flex justify-end items-center"
                                            height="20"
                                            width="20"
                                        />
                                    </span>
                                </div>
                                <div className="mx-auto rounded-[1px] ring-4 my-1 h-[260px] w-[260px] shadow-lg shadow-black ring-offset-0 ring-[#808080]">
                                    <img
                                        src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${id}.png`}
                                        alt={name}
                                        className="w-full h-full object-cover rounded-t-10xl"
                                    />
                                </div>
                                <div className="mx-7 my-1 flex justify-end items-center">
                                    <span className="text-xs font-bold font-Merriweather">NECH-EN026</span>
                                </div>
                                <div className="mx-2">
                                    <div className="bg-[url('https://i.imgur.com/neIKEeX.png')] bg-repeat">
                                        <div className="bg-slate-200 mx-1 my-2 px-1 ring-4 ring-[#FFC85F] font-Merriweather">
                                            <h1 className="font-bold">[Psychic/Tuner/Effect]</h1>
                                            <p className="text-[10px]">{abilityText}</p>
                                            <div className="flex justify-end items-center">
                                                <span>ATK/ {gameIndex !== null ? gameIndex : "???"}</span>
                                                <span>DEF/ {versionName}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="mx-2 flex justify-between items-center">
                                    <span className="text-[9px]">00000001</span>
                                    <span className="text-[9px]">©1996 KAZUKI TAKAHASHI</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
};
